import React, { useEffect, useMemo, useState } from 'react';

import { Tour } from '@enactive-ac/api/src/domain/Tour';
import { DefaultTheme, ThemeProvider } from '@mui/system';
import axiosStatic from 'axios';

import { ThemeContainer } from './components/ThemeContainer';
import { TourSequence } from './components/TourSequence';
import { useEnactiveMetadata } from './hooks/useEnactiveMetadata';
import { TourService } from './services/TourService';

const App: React.FC = () => {
  const metadata = useEnactiveMetadata();
  const tourService = useMemo(() => {
    if (metadata !== undefined) {
      const axios = axiosStatic.create({
        baseURL: `https://${metadata.apiHost}/api/sites/${metadata.siteId}`,
      });
      return new TourService(axios);
    }
  }, [metadata]);
  const [tour, setTour] = useState<Tour>();
  const [theme, setTheme] = useState<DefaultTheme>();

  useEffect(() => {
    if (metadata !== undefined && metadata.enabled) {
      void (async () => {
        const tourData = await tourService?.getTour(metadata.context);
        if (tourData !== undefined) {
          setTheme(tourData.theme);
          setTour(tourData.tour);
        } else {
          setTheme(undefined);
          setTour(undefined);
        }
      })();
    }
  }, [metadata, tourService]);
  return theme !== undefined && tour !== undefined ? (
    <ThemeProvider theme={theme}>
      <ThemeContainer>
        <TourSequence
          onAction={(stepId, _, action) =>
            tourService?.collectTourAction(
              metadata?.context,
              tour.id,
              stepId,
              action,
            )
          }
          onComplete={() => setTour(undefined)}
          tour={tour.steps}
        />
      </ThemeContainer>
    </ThemeProvider>
  ) : (
    <></>
  );
};

export default App;
