import React, { useEffect, useMemo, useState } from 'react';

import { TourStep } from '@enactive-ac/api/src/domain/TourStep';
import { TourStepButton } from '@enactive-ac/api/src/domain/TourStepButton';

import { Mask } from './Mask';
import { TourStepPopup } from './TourStepPopup';

export type TourSequenceProps = {
  onAction: (
    stepId: string,
    stepIndex: number,
    action: TourStepButton['action'],
  ) => void;
  onComplete: () => void;
  tour: TourStep[];
};

export const TourSequence: React.FC<TourSequenceProps> = ({
  onAction,
  onComplete,
  tour,
}) => {
  const [stepIndex, setStepIndex] = useState<number>(-1);
  const [nextStepIndex, setNextStepIndex] = useState<number>();
  const [mutationCount, setMutationCount] = useState<number>(0);
  const [, setCurrentTargetElOffset] = useState<string>();

  const stepInfos = useMemo(() => {
    return tour.map(step => ({
      step,
      targetEl: ((): HTMLElement | null | undefined => {
        switch (step.selector?.type) {
          case 'css':
            return document.querySelector<HTMLElement>(step.selector.value);
          case 'xpath':
            return document.evaluate(
              step.selector.value,
              document,
              null,
              XPathResult.FIRST_ORDERED_NODE_TYPE,
              null,
            ).singleNodeValue as HTMLElement | null;
          case undefined:
            return undefined;
        }
      })(),
    }));
    // We're only using mutationCount to kick a reload of the step infos based
    // on mutation observer events
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tour, mutationCount]);
  const canStart = useMemo(
    () =>
      // We don't want to start this tour until all elements are available. In
      // the future maybe we just go step-by-step waiting for each subsequent
      // element (e.g., part of a tour continuation)
      stepInfos.every(({ targetEl }) => {
        if (targetEl === undefined) {
          // Target element is not appropriate for this tour
          return true;
        }

        // Non-existing or no offset parent (decent check for visibility)
        if (targetEl === null || targetEl.offsetParent === null) {
          return false;
        }

        // Hidden via position or no inner content loaded yet
        const { width, height, x, y } = targetEl.getBoundingClientRect();
        if (width === 0 || height === 0 || x < 0 || y < 0) {
          return false;
        }

        // Style-based visibility, kind of a slow check so do it last
        const style = window.getComputedStyle(targetEl);
        if (style.visibility === 'hidden' || style.display === 'none') {
          return false;
        }

        // This element is loaded and available on the DOM
        return true;
      }),
    [stepInfos],
  );

  useEffect(() => {
    if (!canStart) {
      // We set a mutation count just to trigger a refresh of this node to check
      // again if we can start
      const handler = setInterval(() => setMutationCount(mc => mc + 1), 500);
      return () => clearInterval(handler);
    } else {
      setStepIndex(0);
    }
  }, [canStart]);

  useEffect(() => {
    const targetEl = stepInfos?.[stepIndex]?.targetEl ?? undefined;
    if (targetEl !== undefined) {
      const interval = setInterval(() => {
        const { left, top } = targetEl.getBoundingClientRect();
        setCurrentTargetElOffset(`${left.toString()},${top.toString()}`);
      }, 50);
      return () => clearInterval(interval);
    } else {
      setCurrentTargetElOffset(undefined);
    }
  }, [stepIndex, stepInfos]);

  return canStart ? (
    <>
      {stepInfos.map(({ step, targetEl }, i) => {
        return (
          <div key={step.id}>
            {step.mask ? (
              <Mask
                active={
                  // When the nextStepIndex is set to -1 (i.e., the user just
                  // clicked dismiss / close), then we will become inactive (
                  // start fading out).
                  nextStepIndex !== -1 &&
                  // When this mask belongs to the current step and the user
                  // hasn't clicked next or dismissed
                  ((stepIndex === i && nextStepIndex === undefined) ||
                    // When the user clicked next and this mask belongs to that
                    // mask, we start fading the mask in
                    nextStepIndex === i)
                }
                style={(() => {
                  const padding = 10;
                  const { left, top, width, height } =
                    targetEl?.getBoundingClientRect() ?? {
                      height: 0,
                      left: 0,
                      top: 0,
                      width: 0,
                    };
                  const borderBottomWidth = `calc(100vh - ${String(
                    top + height + padding,
                  )}px)`;
                  return {
                    borderBottomWidth,
                    borderLeftWidth: Math.max(left - padding, 0),
                    borderRightWidth: `calc(100vw - ${String(
                      left + width + padding,
                    )}px)`,
                    borderTopWidth: Math.max(top - padding, 0),
                  };
                })()}
              />
            ) : undefined}
            <TourStepPopup
              active={nextStepIndex === undefined && stepIndex === i}
              buttons={
                step.buttons ??
                [
                  { action: 'dismiss', text: 'Dismiss' },
                  i < tour.length - 1
                    ? { action: 'next', text: 'Next' }
                    : undefined,
                ].filter((b): b is TourStepButton => b !== undefined)
              }
              html={step.html}
              onAction={action => {
                onAction(step.id, i, action);
                switch (action) {
                  case 'close':
                  case 'dismiss':
                    return setNextStepIndex(-1);
                  case 'next':
                    return setNextStepIndex(i + 1);
                }
              }}
              onOut={() => {
                if (nextStepIndex === -1) {
                  onComplete();
                } else if (nextStepIndex !== undefined) {
                  setStepIndex(nextStepIndex);
                  setNextStepIndex(undefined);
                }
              }}
              targetEl={targetEl ?? undefined}
            />
          </div>
        );
      })}
    </>
  ) : (
    <></>
  );
};
