import React from 'react';

import { TourStepButton } from '@enactive-ac/api/src/domain/TourStepButton';

import { ClickAwayListener } from './ClickAwayListener';
import { Popup } from './Popup';

export type TourStepPopupProps = {
  active: boolean;
  buttons: TourStepButton[];
  html: string;
  onAction: (action: TourStepButton['action']) => void;
  onOut?: () => void;
  targetEl?: HTMLElement;
};

export const TourStepPopup: React.FC<TourStepPopupProps> = ({
  active,
  buttons,
  html,
  onAction,
  onOut = () => undefined,
  targetEl,
}) => {
  return (
    <ClickAwayListener onClickAway={() => onAction('close')}>
      <Popup
        anchorEl={targetEl}
        buttons={buttons.map(b => [
          <button
            key={b.action}
            className={`${b.action === 'next' ? 'enactive-primary' : ''}`}
            onClick={() => onAction(b.action)}
          >
            {b.text}
          </button>,
        ])}
        onOutEnd={onOut}
        open={active}
      >
        <div dangerouslySetInnerHTML={{ __html: html }} />
      </Popup>
    </ClickAwayListener>
  );
};
