import React from 'react';

import { createRoot } from 'react-dom/client';

import App from './App';

import 'typeface-roboto';

const rootEl = document.getElementById('enactive-app-root');
if (rootEl === null) {
  throw new Error(
    'Failed to initialize because there was no #enactive-app-root element',
  );
}

const root = createRoot(rootEl);
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
);
